.player-header {
  max-width: $max_width;
  margin: 0px auto;
  //border: 1px solid;
  background-size: cover;
  background-position: center;
  background-color: $primaryColor;
  background-repeat: no-repeat;
  height: toRem(350);
  border-radius: 15px 15px 0px 0px;
}

.player-profile {
  //border: 1px solid;
  width: 100%;
  display: flex;
  border-top: 3px solid $secondaryColor;
  border-bottom: 3px solid $secondaryColor;
  flex-direction: column;
}

.player-profile-image {
  border: 3px solid $secondaryColor;
  width: toRem(175);
  height: toRem(175);
  border-radius: 50%;
  position: absolute;
  left: 50%;
  top: 0%;
  transform: translate(-50%, -30%);
}

.player-profile-image img {
  border-radius: 50%;
}

.profile-left {
  width: 100%;
  background-color: $primaryAccent;
  padding-top: toRem(120);
  border-left: 3px solid $secondaryColor;
  border-right: 3px solid $secondaryColor;
  position: relative;
  padding-left: toRem(15);
  padding-right: toRem(15);
}

.profile-right {
  flex-grow: 1;
  background-color: $white;
  border-right: 3px solid $secondaryColor;
}

.player-name-desktop {
  display: none;
}

.player-profile-content {
  border-left: 3px solid $secondaryColor;
}

.player-name-mobile {
  color: $primaryColor;
}

.seasons {
  display: flex;
  flex-wrap: wrap;
}

.player-list {
  display: flex;
}

.season-container {
  position: relative;
  width: 3rem;
  margin: toRem(5);
}

.season {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 10;
  font-weight: 600;
  color: $primaryColor;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  object-fit: cover;
  text-align: center;
}

.season-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 10;
  font-weight: 600;
  color: $primaryColor;
  border-radius: 50%;
  object-fit: cover;
  text-align: center;
}

.season img {
  border-radius: 50%;
}

@keyframes fill {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(135deg);
  }
}
@keyframes growProgressBar {
  0%,
  33% {
    --pgPercentage: 0;
  }
  100% {
    --pgPercentage: var(--value);
  }
}

@property --pgPercentage {
  syntax: "<number>";
  inherits: false;
  initial-value: 0;
}

div[role="progressbar"] {
  --size: 3rem;
  --fg: #35546e;
  --bg: #d4e6f3;
  --pgPercentage: var(--value);
  animation: growProgressBar 3s 1 forwards;
  width: var(--size);
  height: var(--size);
  border-radius: 50%;
  display: grid;
  place-items: center;
  background: radial-gradient(closest-side, rgb(255, 191, 15) 80%, transparent 0 99.9%, white 0), conic-gradient(var(--fg) calc(var(--pgPercentage) * 1%), var(--bg) 0);
  font-family: Helvetica, Arial, sans-serif;
  font-size: calc(var(--size) / 3.5);
  font-weight: 600;
  line-height: 4;
  color: var(--fg);
}

div[role="progressbar"]::before {
  counter-reset: percentage var(--value);
}

.player-socials {
  display: flex;
}

.player-socials > div {
  margin-right: toRem(5);
}
.player-socials img {
  height: toRem(30);
  width: toRem(30);
}

.player-profile-content {
  padding: toRem(10);
}

.spoiler-block {
  width: 100%;
  margin: 10px auto;
}

.season-standings {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.standing-contain {
  //border: 2px solid $secondaryColor;
  position: relative;
  border-radius: 50%;
  width: 33%;
  width: toRem(125);
  height: toRem(125);
}

.sc__banner {
  position: absolute;
  background-color: $secondaryColor;
  width: 95%;
  top: 10px;
  left: 50%;
  transform: translateX(-50%);
  text-align: center;
  font-size: toRem(12);
  font-weight: 600;
  border-radius: 5px;
  color: $primaryColor;
  box-shadow: 2px 2px 10px black;
  z-index: 10;
}

.sc__hoh img,
.sc__pov img {
  border-radius: 50%;
  left: 50%;
  transform: translateX(-50%);
  border: 2px solid $secondaryColor;

  position: absolute;
}

.sc__pov {
}

.nom1 {
  position: absolute;
  height: 50%;
  width: 100%;
  border-radius: 150px 150px 0 0;
  background-color: green;
  border-bottom: 2px solid $secondaryColor;
}

.nom2 {
  position: absolute;
  bottom: 0;
  height: 50%;
  width: 100%;
  border-radius: 0 0 150px 150px;
  background-color: green;
  border-bottom: 2px solid $secondaryColor;
}

.p-t-image {
  height: toRem(45);
  border-radius: 50%;
}
